<template>
<div class="user-page">
  <Card class="user-form">
    <p slot="title">
      <strong>使用邮箱地址注册</strong>
    </p>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate"
      label-position="top">
      <FormItem label="电子邮箱" prop="email">
        <Input v-model="formValidate.email" placeholder="请输入邮箱"></Input>
      </FormItem>
      <FormItem label="用户名（昵称，不可修改）" prop="name">
        <Input v-model="formValidate.name" placeholder="请输入用户名"></Input>
      </FormItem>
      <FormItem label="密码" prop="passwd">
        <Input type="password" v-model="formValidate.passwd"></Input>
      </FormItem>
      <FormItem label="密码确认" prop="passwdCheck">
        <Input type="password" v-model="formValidate.passwdCheck"></Input>
      </FormItem>
      <FormItem class="t-center">
        <Button type="primary" @click="handleSubmit('formValidate')">注册</Button>
        <Button @click="handleReset('formValidate')" style="margin-left: 8px">清空</Button>
      </FormItem>
    </Form>
    <div class="user-form-tip t-center">
      <p>注册即表示您同意我们的<a href="/#/privacy" class="">服务条款与隐私政策</a></p>
      <p>已经拥有账户？ <a href="/#/user-signin" class="">登录</a></p>
    </div>
  </Card>
</div>
</template>
<script>
import apiClient from '@/api/index'
import utilTool from '@/util/index'
export default {
  data () {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.formValidate.passwdCheck !== '') {
          // 对第二个密码框单独验证
          this.$refs.formValidate.validateField('passwdCheck')
        }
        callback()
      }
    }
    const validatePassCheck = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.formValidate.passwd) {
        callback(new Error('两次密码不匹配'))
      } else {
        callback()
      }
    }
    return {
      formValidate: {
        email: '',
        name: '',
        passwd: '',
        passwdCheck: ''
      },
      ruleValidate: {
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '邮箱地址非法', trigger: 'blur' },
          { type: 'string', max: 32, message: '最多输入32个字符', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { type: 'string', max: 32, message: '最多输入32个字符', trigger: 'blur' }
        ],
        passwd: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' },
          { type: 'string', min: 6, message: '最少输入6个字符', trigger: 'blur' },
          { type: 'string', max: 32, message: '最多输入32个字符', trigger: 'blur' }
        ],
        passwdCheck: [
          { required: true, message: '请确认输入密码', trigger: 'blur' },
          { validator: validatePassCheck, trigger: 'blur' },
          { type: 'string', min: 6, message: '最少输入6个字符', trigger: 'blur' },
          { type: 'string', max: 32, message: '最多输入32个字符', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    if (utilTool.getUserToken()) {
      this.$router.push({
        name: 'video-list'
      })
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          apiClient.signUp(this.formValidate).then(res => {
            if (res.user_id) {
              this.$Message.success('注册成功')
              utilTool.setUserToken(res)
              this.$router.push({
                name: 'video-list'
              })
            } else {
              this.$Message.error('注册失败')
            }
          }).catch(err => {
            if (err.message === 'Unprocessable Entity') {
              this.$Message.error('邮箱地址已注册')
            } else {
              this.$Message.error(err.message)
            }
          })
        } else {
          // this.$Message.error('请检查表单是否完成')
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    }
  },
  components: {
  }
}
</script>

<style scoped>
.user-page {
  height: 100%;
  padding: 30px 0;
  text-align: center;
}
.user-form {
  width: 350px;
  margin: auto;
  text-align: left;
}
.t-center {
  text-align:center;
}
</style>



