import { config } from '@/../package.json'
import moment from 'moment'

function getLogo () {
  return config.logo
}

function formatTime (timestamp) {
  return moment(timestamp).format('YYYY-MM-DD HH:mm:ss')
}

function formatTimeToDate (timestamp) {
  return moment(timestamp).format('YYYY-MM-DD')
}

function getDefaultImg () {
  return config.default404_img
}

function getDefaultImgThumb () {
  return config.default404_img_thumb
}

function guid () {
  return 'xxxxx-yxxx'.replace(/[xy]/g, function (c) {
    var r = Math.random() * 16 | 0
    var v = c === 'x' ? r : (r & 0x3 | 0x8)
    return v.toString(16)
  })
}

function getIframeHtmlStr (url) {
  return `<iframe width="100%" height="100%" src="${url}" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`
}

function getPlayerUrl (videoId) {
  const homePage = getHomePage()
  return `${homePage}static/player.html?video_id=${videoId}`
}

function getHomePage () {
  let homePage = location.origin + (location.pathname || '')
  if (!homePage.endsWith('/')) {
    homePage = homePage + '/'
  }
  return homePage
}

function setUserToken (token) {
  if (token && token.token) {
    localStorage.setItem('user', JSON.stringify(token))
  } else {
    localStorage.setItem('user', '')
  }
}

function getUserToken () {
  var tokenStr = localStorage.getItem('user')
  if (tokenStr) {
    var tokenObj = null
    try {
      tokenObj = JSON.parse(tokenStr)
    } catch (error) {
      console.log(error)
      return null
    }
    return tokenObj
  } else {
    return null
  }
}

function injectConfig () {
  localStorage.setItem('config', JSON.stringify(config))
}

function formatSeconds (value) {
  let result = parseInt(value)
  let h = Math.floor(result / 3600) < 10 ? '0' + Math.floor(result / 3600) : Math.floor(result / 3600)
  let m = Math.floor((result / 60 % 60)) < 10 ? '0' + Math.floor((result / 60 % 60)) : Math.floor((result / 60 % 60))
  let s = Math.floor((result % 60)) < 10 ? '0' + Math.floor((result % 60)) : Math.floor((result % 60))
  if (h === '00') {
    result = `${m}:${s}`
  } else {
    result = `${h}:${m}:${s}`
  }
  return result
}

function isMobile () {
  var sUserAgent = navigator.userAgent.toLowerCase()
  var bIsIpad = sUserAgent.match(/ipad/i)
  var bIsIphoneOs = sUserAgent.match(/iphone os/i)
  var bIsMidp = sUserAgent.match(/midp/i)
  var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i)
  var bIsUc = sUserAgent.match(/ucweb/i)
  var bIsAndroid = sUserAgent.match(/android/i)
  var bIsCE = sUserAgent.match(/windows ce/i)
  var bIsWM = sUserAgent.match(/windows mobile/i)
  var mobile = sUserAgent.match(/mobile/i)
  if (bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM || mobile) {
    return true
  } else {
    return false
  }
}

export default {
  getLogo,
  formatTime,
  formatTimeToDate,
  getDefaultImg,
  getDefaultImgThumb,
  guid,
  getIframeHtmlStr,
  getPlayerUrl,
  getHomePage,
  getUserToken,
  setUserToken,
  injectConfig,
  formatSeconds,
  isMobile
}



