<template>
<Menu :active-name="activeName" width="auto" style="text-align: left;">
  <MenuGroup :title="item.title" v-for="(item, index) in menuList" :key="index">
    <MenuItem :name="mitem.name" :to="mitem.to" v-show="!mobile || mitem.mobile >= 0"
      v-for="(mitem, mindex) in item.menuItems" :key="mindex">
      <Badge :text="mobile && mitem.mobile === 1 ? 'mobile': ''">
        <span class="menu-item">
          <Icon :type="mitem.icon" v-if="mitem.icon"/>
          {{mitem.title}}
        </span>
      </Badge>
    </MenuItem>
  </MenuGroup>
</Menu>
</template>

<script>
export default {
  props: {
    menuList: {
      type: Array,
      default: () => {
        return []
      }
    },
    activeName: {
      type: String,
      default: ''
    },
    mobile: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  },
  mounted () {
  },
  computed: {
  },
  methods: {
  }
}
</script>

<style>
.menu-item {
  padding: 0 1.8rem;
}
</style>



