<template>
<div class="user-page">
  <Card class="user-form">
    <p slot="title">
      <strong>登录 mh12306.com </strong>
    </p>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate"
      label-position="top">
      <FormItem label="电子邮箱" prop="email">
        <Input v-model="formValidate.email" placeholder="请输入邮箱"></Input>
      </FormItem>
      <FormItem label="密码" prop="passwd">
        <Input type="password" v-model="formValidate.passwd"></Input>
      </FormItem>
      <FormItem class="t-center">
        <Button type="primary" @click="handleSubmit('formValidate')">登录</Button>
        <Button @click="handleReset('formValidate')" style="margin-left: 8px">清空</Button>
      </FormItem>
    </Form>
    <div class="user-form-tip t-center">
      <!--<p><a href="/#/recover" class="">忘记密码？</a></p>-->
      <p>尚未拥有账户？ <a href="/#/user-signup" class="">注册</a></p>
    </div>
  </Card>
</div>
</template>
<script>
import apiClient from '@/api/index'
import utilTool from '@/util/index'
export default {
  data () {
    return {
      formValidate: {
        email: '',
        passwd: ''
      },
      ruleValidate: {
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '邮箱地址非法', trigger: 'blur' },
          { type: 'string', max: 32, message: '最多输入32个字符', trigger: 'blur' }
        ],
        passwd: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { type: 'string', min: 6, message: '最少输入6个字符', trigger: 'blur' },
          { type: 'string', max: 32, message: '最多输入32个字符', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    if (utilTool.getUserToken()) {
      this.$router.push({
        name: 'video-list'
      })
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          apiClient.signIn(this.formValidate).then(res => {
            if (res.user_id) {
              this.$Message.success('登录成功')
              utilTool.setUserToken(res)
              this.$router.push({
                name: 'video-list'
              })
            } else {
              this.$Message.error('登录失败')
            }
          }).catch(err => {
            this.$Message.error(err.message)
          })
        } else {
          // this.$Message.error('请检查表单是否完成')
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    }
  },
  components: {
  }
}
</script>

<style scoped>
.user-page {
  height: 100%;
  padding: 30px 0;
  text-align: center;
}
.user-form {
  width: 350px;
  margin: auto;
  text-align: left;
}
.t-center {
  text-align:center;
}
</style>



