<template>
  <div id="app">
    <pageHeader :user="user" :mobile="mobile"></pageHeader>
    <div class="main-title">
      <h1>免费在线图床</h1>
      <h2>Free Image Host</h2>
      <h3>mh12306 梅花12306图床-高速稳定的图片上传和外链服务</h3>
      <h3>最大单张支持 5 MB</h3>
      <h3>本站启用自动检测技术, 禁止上传色情/反动/博彩/黑产等违法图片</h3>
    </div>
    <Row class="wrapper-container" v-if="!mobile" >
      <router-view />
    </Row>
    <div v-if="mobile" class="mwrapper-container">
      <div class="menu-drawer">
        <Button @click="menuShow = true" type="primary">
          导航
        </Button>
        <Drawer title="导航" :closable="false" v-model="menuShow" placement="left">
          <pageMenu :mobile="true" :activeName="activeName" :menuList="menuList"></pageMenu>
        </Drawer>
      </div>
      <Card :bordered="false" :padding="5">
        <router-view />
      </Card>
    </div>
    <BackTop></BackTop>
    <pageFooter></pageFooter>
  </div>
</template>

<script>
import pageHeader from '@/components/page_header'
import pageFooter from '@/components/page_footer'
import pageMenu from '@/components/page_menu'
import utilTool from '@/util/index'
export default {
  name: 'App',
  data () {
    return {
      menuShow: false,
      user: null,
      activeName: 'profile',
      menuList: [
        {
          title: '上手',
          menuItems: [{
            title: '须知事项',
            name: 'preknow',
            mobile: 0,
            to: {
              name: 'preknow'
            }
          }]
        },
        {
          title: '文档',
          menuItems: [{
            title: '简介',
            name: 'profile',
            mobile: 0, // 1 标红 -1 手机不显示
            to: {
              name: 'profile'
            }
          }, {
            title: 'Api接口',
            name: 'api',
            mobile: 0,
            to: {
              name: 'api'
            }
          }]
        }
      ]
    }
  },
  mounted () {
    this.$Message.config({
      top: 150
    })
    this.activeName = this.$route.name
    this.user = utilTool.getUserToken()
  },
  watch: {
    $route (to) {
      this.activeName = to.name
      this.menuShow = false
      this.user = utilTool.getUserToken()
    }
  },
  computed: {
    mobile () {
      return utilTool.isMobile()
    }
  },
  components: {
    pageHeader,
    pageFooter,
    pageMenu
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  height: 100%;
}
body {
  height: 100%;
  background-color: #eee!important;
}
html {
  height: 100%;
}
.main-title{
  margin-top: 50px;
  color: #fff;
  padding: 20px;
  background-image: linear-gradient(to right, #E13896, #157592 20%, #A89215 80%, #1574A8);
  height: 200px;
}
.wrapper-container {
  margin: 30px auto!important;
  width: 70%;
  background: #fff;
  margin: 75px auto 15px;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,.1);
  min-height: 80%;
}
.mwrapper-container {
  margin: 75px auto 15px;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,.1);
  min-height: 80%;
}
.menu-drawer {
  padding: 0 5% 15px;
  text-align: right;
}
.color-primary {
  color: #2d8cf0;
}
.color-warning {
  color: #ff9900;
}
.color-error {
  color: #ed4014;
}
.color-success {
  color: #19be6b;
}
.common-page {
  padding: 30px 10%;
  text-align: left;
}
.common-page h1 {
  text-align: center;
}
.common-page h3 {
  margin: 12px 0;
}
.common-page p {
  margin: 6px 0;
  color: #515a6e;
  text-align: justify;
}
</style>



