<template>
<div class="wrapper-header">
  <div class="wrapper-header-logo">
    <a :href="homePage">
      <img :src="logoUrl" class="wrapper-header-logo-img">
      {{ mobile ? 'mh' : 'mh12306.com' }}
    </a>
    <div class="apps">
    </div>
  </div>
  <div class="wrapper-header-menu">
  </div>
  <div class="wrapper-header-right">
    <div class="wrapper-header-right-sign">
      <Poptip placement="bottom" v-if="user && user.name">
        <a>{{user.name}}</a>
        <div slot="content">
          UserID: {{user.user_id}}
        </div>
      </Poptip>
      <!--
        <a href="#/about" class="">关于</a>
      -->
      <a href="#/api" class="">API</a>
      <a href="#/privacy" class="">服务条款</a>
      <span class="wrapper-header-right-sign-dot">·</span>
      <a v-show="user" class="" @click="logout">退出</a>
      <a v-show="!user" href="/#/user-signin" class="">登录</a>
    </div>
  </div>
</div>
</template>

<script>
import utilTool from '@/util/index'
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return null
      }
    },
    mobile: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      logoUrl: '',
      homePage: '/'
    }
  },
  mounted () {
    this.logoUrl = utilTool.getLogo()
    this.homePage = utilTool.getHomePage()
  },
  computed: {
  },
  methods: {
    logout () {
      this.$Modal.confirm({
        title: `注销`,
        content: `确认注销用户登录状态?`,
        onOk: () => {
          utilTool.setUserToken(null)
          this.$router.go(0)
        }
      })
    }
  }
}
</script>

<style>
a {
  padding: 0 15px;
  font-weight: bold;
  color: #2d8cf0;
  background: 0 0;
  text-decoration: none;
  outline: 0;
  cursor: pointer;
  transition: color .2s ease;
}
.wrapper-header {
  width: 100%;
  height: 50px;
  padding: 0 40px;
  box-sizing: border-box;
  z-index: 1000;
  position: fixed;
  top: 0;
  background-color: #fff;
  box-shadow: 0 1px 3px rgba(26,26,26,.1);
  font-size: 14px;
}
.wrapper-header-logo-img {
  height: 32px;
  vertical-align: middle;
  float: left;
  margin-top: 8px;
}
.apps {
  display: inline-block;
  float: left;
  margin-left: 22px;
  padding-left: 12px;
}
.wrapper-header-logo {
  float: left;
  height: 50px;
  line-height: 48px;
}
.wrapper-header-logo a {
  font-weight: bold;
}
.wrapper-header-menu {
  float: left;
  margin-left: 32px;
  height: 50px;
  line-height: 48px;
}
.wrapper-header-menu a {
  color: #808695;
  margin-right: 16px;
}
.wrapper-header-right {
  float: right;
  margin-right: 30px;
}
.wrapper-header-right-sign {
  height: 50px;
  line-height: 48px;
}
@media screen and (max-width:700px) {
  .wrapper-header {
    padding: 0 10px;
  }
}
@media screen and (min-width:700px) {
  .wrapper-header {
    padding: 0 20px;
  }
}
@media screen and (min-width:900px) {
  .wrapper-header {
    padding: 0 30px;
  }
}
@media screen and (min-width:1300px) {
  .wrapper-header {
    padding: 0 40px;
  }
}
</style>



