import axios from 'axios'
import { config } from '@/../package.json'
// import { Message } from 'view-design'
import utilTool from '@/util/index'

const env = process.env.NODE_ENV
const apiHost = config[env].api_host
// console.log('env', env, apiHost)

const requestClient = axios.create({
  timeout: 30000
})
requestClient.interceptors.request.use(function (reqConfig) {
  const user = utilTool.getUserToken() || {}
  const token = user.token
  if (token) {
    reqConfig.headers.authorization = `Bearer ${token}`
  }
  return reqConfig
}, function (error) {
  return Promise.reject(error)
})
requestClient.interceptors.response.use(function (response) {
  return response.data || {}
}, function (error) {
  var response = error.response || {}
  var data = response.data || {
    message: error.message
  }
  if (data.message === 'Authentication Error') {
    data.message = '登录状态已失效, 请重新登录'
  }
  if (data.message === 'Unauthorized') {
    data.message = '无权限'
  }
  // Message.error(data.message || 'unkonw error')
  return Promise.reject(data)
})

function getUploadAction (type) {
  var url = apiHost + `/tool/img_upload`
  return url
}

function signUp (user) {
  var url = apiHost + '/user/signup'
  return requestClient.post(url, user)
}

function signIn (user) {
  var url = apiHost + '/user/signin'
  return requestClient.post(url, user)
}

export default {
  getUploadAction,
  signUp,
  signIn
}



