import Vue from 'vue'
import Router from 'vue-router'
// import aboutPage from '@/view/about'
import privacyPage from '@/view/privacy'
import mainPage from '@/view/main'
import apiPage from '@/view/api'
import userSigninPage from '@/view/user-signin'
import userSignupPage from '@/view/user-signup'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: mainPage
    },
    {
      path: '/privacy',
      name: 'privacy',
      component: privacyPage
    },
    {
      path: '/api',
      name: 'api',
      component: apiPage
    },
    {
      path: '/user-signin',
      name: 'user-signin',
      component: userSigninPage
    },
    {
      path: '/user-signup',
      name: 'user-signup',
      component: userSignupPage
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



