<template>
<div>
<Row v-if="!!uploadFile">
  <Col span="12">
    <img :src="uploadFile" style="max-height: 180px;"/>
    <div>
      <Button icon="ios-trash-outline" type="error" @click="removeImg">删除图片</Button>
    </div>
  </Col>
  <Col span="12">
      <Form label-position="top">
        <FormItem label="图片地址">
          <Input :value="uploadFile">
            <Button slot="append" type="primary" @click="copy(uploadFile)">复制</Button>
          </Input>
        </FormItem>
        <FormItem label="HTML">
          <Input :value="htmlImg">
            <Button slot="append" type="primary" @click="copy(htmlImg)">复制</Button>
          </Input>
        </FormItem>
        <FormItem label="BBCode">
          <Input :value="bbCodeImg">
            <Button slot="append" type="primary" @click="copy(bbCodeImg)">复制</Button>
          </Input>
        </FormItem>
        <FormItem label="Markdown">
          <Input :value="markdownImg">
            <Button slot="append" type="primary" @click="copy(markdownImg)">复制</Button>
          </Input>
        </FormItem>
      </Form>
  </Col>
</Row>

<div v-show="!uploadFile">
  <Upload type="drag" class="upload-comp" ref="upload-comp" :action="uploadAction" accept="image/*"
    :max-size="maxSize" :before-upload="checkUpload"
    :on-success="handleUploadImgSuc" :on-error="handleUploadImgError" :on-remove="handleUploadImgRemove">
    <div style="padding: 20px 0">
        <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
        <h2 style="text-align: center;">点击或者拖拽图片到此区域内上传</h2>
        <p style="text-align: center;">Click or drag files here to upload</p>
    </div>
  </Upload>
</div>
</div>
</template>

<script>
import copyTo from 'copy-to-clipboard'
import apiClient from '@/api/index'
export default {
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      uploadFile: this.value,
      maxSize: 5 * 1024 * 1024,
      linkModal: false,
      linkValue: ''
    }
  },
  watch: {
    value (val) {
      this.uploadFile = val
    }
  },
  computed: {
    uploadAction () {
      return apiClient.getUploadAction()
    },
    htmlImg () {
      return `<img src="${this.uploadFile}" border="0" />`
    },
    bbCodeImg () {
      return `[img]${this.uploadFile}[/img]`
    },
    markdownImg () {
      return `![yourimgname](${this.uploadFile})`
    }
  },
  methods: {
    copy (value) {
      copyTo(value)
      this.$Message.success({
        background: true,
        content: '成功复制到粘贴板'
      })
    },
    handleUploadImgSuc (res, file) {
      if (res.url) {
        this.uploadFile = res.url
        this.$emit('input', res.url)
      } else {
        this.uploadFile = ''
        this.$emit('input', '')
      }
    },
    handleUploadImgError (err, file) {
      console.log('upload error', err)
      this.uploadFile = ''
      this.$emit('input', '')
    },
    handleUploadImgRemove (file, fileList) {
      this.uploadFile = ''
      this.$emit('input', '')
    },
    checkUpload () {
      if (this.uploadFile) {
        return false
      } else {
        return true
      }
    },
    removeImg () {
      this.$refs['upload-comp'].clearFiles()
      this.uploadFile = ''
      this.$emit('input', '')
    }
  }
}
</script>

<style scoped>
.upload-comp {
}
</style>



